.header {
	margin-top: .5rem;
	padding-left: 10px;
}

.header ul li {
	list-style: none;
	margin: 0;
	padding: .5rem 1rem;
	border-radius: 3px;
}

.header ul li:hover {
	background-color: #444;
}
.header ul li Link:hover {
	color: #00bc8c;
}


.header a:hover {
	text-decoration: none;
}

.header h3 a {
	color: white;
}

.header h3 a:hover {
	color: #00bc8c;
}
	@keyframes lds-double-ring {
	  0% {
	    -webkit-transform: rotate(0);
	    transform: rotate(0);
	  }
	  100% {
	    -webkit-transform: rotate(360deg);
	    transform: rotate(360deg);
	  }
	}
	@-webkit-keyframes lds-double-ring {
	  0% {
	    -webkit-transform: rotate(0);
	    transform: rotate(0);
	  }
	  100% {
	    -webkit-transform: rotate(360deg);
	    transform: rotate(360deg);
	  }
	}
	@keyframes lds-double-ring_reverse {
	  0% {
	    -webkit-transform: rotate(0);
	    transform: rotate(0);
	  }
	  100% {
	    -webkit-transform: rotate(-360deg);
	    transform: rotate(-360deg);
	  }
	}
	@-webkit-keyframes lds-double-ring_reverse {
	  0% {
	    -webkit-transform: rotate(0);
	    transform: rotate(0);
	  }
	  100% {
	    -webkit-transform: rotate(-360deg);
	    transform: rotate(-360deg);
	  }
	}
	.lds-double-ring {
	  position: relative;
	}

	.lds-double-ring div {
	  position: absolute;
	  width: 160px;
	  height: 160px;
	  top: 20px;
	  left: 20px;
	  border-radius: 50%;
	  border: 8px solid #000;
	  border-color: #2726bb transparent #2726bb transparent;
	  -webkit-animation: lds-double-ring 0.8s linear infinite;
	  animation: lds-double-ring 0.8s linear infinite;
	}
	.lds-double-ring div:nth-child(2) {
	  width: 140px;
	  height: 140px;
	  top: 30px;
	  left: 30px;
	  border-color: transparent #b11414 transparent #b11414;
	  -webkit-animation: lds-double-ring_reverse 0.8s linear infinite;
	  animation: lds-double-ring_reverse 0.8s linear infinite;
	}
	.lds-double-ring {
	  left: 30rem;
	  width: 100px !important;
	  height: 100px !important;
	  -webkit-transform: translate(-50px, -50px) scale(0.5) translate(50px, 50px);
	  transform: translate(-50px, -50px) scale(0.5) translate(50px, 50px);
	}
